import React, { useEffect, useMemo, useState } from "react";

import clsx from "clsx";
import { useGetLocale } from "providers";

import { ReactComponent as GoldMedal } from "assets/icons/gold-medal.svg";
import { ReactComponent as SilverMedal } from "assets/icons/silver-medal.svg";
import { ReactComponent as CooperMedal } from "assets/icons/cooper-medal.svg";

import {
  Avatar,
  LinkWithSearch,
  LinkWithSearchProps,
  Table,
  Typography,
} from "components";
import { useFetch, useInterval } from "hooks";
import { getIntlValue, useWindowSize } from "providers";

import { setPlayerSearchParams } from "components/LinkWithSearch/utils";
import css from "./style.module.scss";

import { Scrollbar } from "components/Scrollbar";
import { useLocation } from "react-router-dom";

const medals = [GoldMedal, SilverMedal, CooperMedal];
const mobileScreen = parseInt(css.mobileScreen);

export interface RankingElement {
  user_name: string;
  net_profit_usd: string;
  total_wagered_usd: string;
  profile_picture_url: string;
}

interface RankingProps {
  open?: boolean;
}

export interface FetchMonthlyLeaderboard {
  success: boolean;
  leaderboard: RankingElement[];
  message: string;
}

const avatarStyleMini = { width: 32, height: 32 };
const avatarStyleLarge = { width: 48, height: 48 };
const scrollStyle = { height: 450, width: "100%" };
const mobileScrollStyle = { height: 520, width: "100%" };

const Ranking = React.memo(({ open }: RankingProps) => {
  const intl = useGetLocale();
  const [rankingData, setRankingData] = useState<RankingElement[]>([]);
  const [first, second, third] = rankingData || [];
  const location = useLocation();
  const { width } = useWindowSize();
  const isMobile = width < mobileScreen;
  const [fetchDailyLeaderboard, dailyLeaderboardResult] =
    useFetch<FetchMonthlyLeaderboard>("statistics/monthly-leaderboard");

  const getTableHeaderName = getIntlValue(
    intl,
    "gameTemplate.statistics.table.",
  );

  useEffect(() => {
    if (open) {
      fetchDailyLeaderboard();
    }
  }, [fetchDailyLeaderboard, open]);

  useInterval(() => {
    if (open) {
      fetchDailyLeaderboard();
    }
  }, 30000);

  useEffect(() => {
    if (!dailyLeaderboardResult.data?.leaderboard || !open) {
      return;
    }

    const leaderboard = dailyLeaderboardResult.data.leaderboard;

    if (JSON.stringify(rankingData) !== JSON.stringify(leaderboard)) {
      setRankingData(leaderboard);
    }
  }, [dailyLeaderboardResult.data]);

  const searchParams = useMemo(
    (): { [key: string]: LinkWithSearchProps } => ({
      first: {
        search: { name: "page", value: "profile" },
        searchParams: {
          user: first?.user_name,
        },
      },
      second: {
        search: { name: "page", value: "profile" },
        searchParams: {
          user: second?.user_name,
        },
      },
      third: {
        search: { name: "page", value: "profile" },
        searchParams: {
          user: third?.user_name,
        },
      },
    }),
    [first, second, third],
  );

  return (
    <div className={css.wrapper}>
      <div className={clsx(css.topPlaces, css.flex, css.row)}>
        <LinkWithSearch {...searchParams.second}>
          <div className={clsx(css.secondaryPlace, css.flex, css.relative)}>
            <div className={clsx(css.circle, css.userLogo)}>
              <Avatar
                userName={second?.user_name}
                style={avatarStyleMini}
                avatar={second?.profile_picture_url}
              />
            </div>

            <Typography className={clsx(css.userName)} variant="subtitle1">
              {second?.user_name}
            </Typography>

            <SilverMedal className={css.medal} />
          </div>
        </LinkWithSearch>

        <LinkWithSearch {...searchParams.first}>
          <div className={clsx(css.topPlace, css.flex, css.relative)}>
            <div className={clsx(css.circle, css.topUserLogo)}>
              <Avatar
                userName={first?.user_name}
                style={avatarStyleLarge}
                avatar={first?.profile_picture_url}
              />
            </div>

            <Typography className={clsx(css.userName)} variant="subtitle1">
              {first?.user_name}
            </Typography>

            <GoldMedal className={css.medal} />
          </div>
        </LinkWithSearch>

        <LinkWithSearch {...searchParams.third}>
          <div className={clsx(css.secondaryPlace, css.flex, css.relative)}>
            <div className={clsx(css.circle, css.userLogo)}>
              <Avatar
                userName={third?.user_name}
                style={avatarStyleMini}
                avatar={third?.profile_picture_url}
              />
            </div>

            <Typography className={clsx(css.userName)} variant="subtitle1">
              {third?.user_name}
            </Typography>

            <CooperMedal className={css.medal} />
          </div>
        </LinkWithSearch>
      </div>
      <div className={css.tableWrapper}>
        <Scrollbar style={isMobile ? mobileScrollStyle : scrollStyle}>
          <Table
            head={{
              values: [
                getTableHeaderName("rank"),
                getTableHeaderName("player"),
                getTableHeaderName("totalWagered"),
                getTableHeaderName("netProfit"),
              ],
            }}
            body={{
              rows: rankingData?.map(
                (
                  {
                    net_profit_usd,
                    profile_picture_url,
                    total_wagered_usd,
                    user_name,
                  },
                  index,
                ) => {
                  const Medal = medals[index];

                  return {
                    values: [
                      <div className={css.tableCellWrapper}>
                        {Medal ? <Medal /> : index + 1}
                      </div>,

                      <LinkWithSearch
                        {...setPlayerSearchParams(user_name)}
                        className={clsx(
                          css.tableCellWrapper,
                          css.userNameCell,
                        )}>
                        <div
                          className={clsx(
                            css.circle,
                            css.userLogo,
                            css.tableAvatar,
                          )}>
                          <Avatar
                            userName={user_name}
                            style={avatarStyleMini}
                            avatar={profile_picture_url}
                          />
                        </div>
                        <span className={css.userNameUnit}>{user_name}</span>
                      </LinkWithSearch>,

                      total_wagered_usd + " USD",
                      net_profit_usd + " USD",
                    ],
                    key: user_name,
                  };
                },
              ),
            }}
          />
        </Scrollbar>
      </div>
    </div>
  );
});

export default Ranking;
